<template>
  <ag-grid-vue
    class="ag-theme-alpine"
    :style="{ width, height }"
    :defaultColDef="newDefaultColDef"
    :columnDefs="columnDefs"
    :overlayLoadingTemplate="overlayLoadingTemplate"
    :overlayNoRowsTemplate="overlayNoRowsTemplate"
    :rowData="rowData"
    :rowSelection="rowSelection"
    :pagination="pagination"
    :paginationPageSize="paginationPageSize"
    @grid-ready="onGridReady"
    @cell-clicked="cellClicked"
    @cell-doubleclicked="cellDoubleClicked"
    @rowData-updated="rowDataUpdated"
  >
  </ag-grid-vue>
</template>

<script>
import { AgGridVue } from "ag-grid-vue3";

export default {
  name: 'ui-grid',
  components: {
    AgGridVue,
  },
  props: {
    // Grid Width
    width: {
      type: String,
      default: '100%',
    },
    // Grid Height
    height: {
      type: String,
      default: '500px',
    },
    //  Column 
    defaultColDef: {
      type: Object,
      default: () => ({}),
    },
    // Column 
    columnDefs: {
      type: Array,
    },
    //  
    overlayLoadingTemplate: {
      type: String,
      default: `<span class="ag-overlay-loading-center">Loading...</span>`,
    },
    // row   
    overlayNoRowsTemplate: {
      type: String,
      default: `<span>  .</span>`,
    },
    // rowData
    rowData: {
      type: Array,
    },
    // rowNum  Index
    rowNumIndex: Number,
    //   Index
    checkboxIndex: Number,
    // rowSelection  (multiple:  )
    rowSelection: {
      type: String,
      default: 'multiple',
    },
    // Pagination  
    pagination: {
      type: Boolean,
      default: false,
    },
    //   row  
    paginationPageSize: {
      type: Number,
      default: 20,
    },
  },
  data() {
    return {
      gridApi: null,
      newDefaultColDef: {},
      originDefaultColDef: {
        editable: false,
        sortable: true,
        resizable: true,
        filter: true,
      },
    }
  },
  beforeMount() {
    // defaultColDef 
    Object.assign(this.newDefaultColDef, this.originDefaultColDef, this.defaultColDef);

    // columnDefs 
    this.setColumnDefs();

    // columnDefs  
    Object.values(this.columnDefs)
      .filter(v => v.align)
      .map(v => {
        switch (v.align) {
          case 'left':
            v.cellClass = 'grid-cell-left';
            break;
          case 'center':
            v.cellClass = 'grid-cell-center';
            break;
          case 'right':
            v.cellClass = 'grid-cell-right';
            break;
        }
        delete v.align;
      });
  },
  methods: {
    onGridReady(params) {
      this.gridApi = params.api;
      this.gridApi.sizeColumnsToFit();

      this.gridApi.__proto__.getRowDatas = () => this.getRowDatas();
      this.gridApi.__proto__.removeSelectedRows = () => this.removeSelectedRows();

      this.$emit('onGridReady', this.gridApi);
    },
    // row  
    cellClicked(params) {
      this.$emit('cellClicked', params);
    },
    // row  
    cellDoubleClicked(params) {
      this.$emit('cellDoubleClicked', params);
    },
    //  rows 
    removeSelectedRows() {
      const rows = this.gridApi.getSelectedRows();
      return this.gridApi.updateRowData({ remove: rows });
    },
    // row 
    getRowDatas() {
      return this.rowData;
    },
    // rowData   
    rowDataUpdated() {
      this.setColumnDefs();
    },
    // columnDefs 
    setColumnDefs() {
      this.columnDefs.map((c,i) => {
        if (null !== this.rowNumIndex && i === this.rowNumIndex) {
          c.field = 'No';
          c.width = c.width || 15;
          this.rowData.map((r,j) => r.No = j+1);
        }
        
        if (null !== this.checkboxIndex && i === this.checkboxIndex) {
          c.field = '';
          c.width = 50;
          c.headerCheckboxSelection = true;
          c.checkboxSelection = true;
        }
      });
    },
  },
}
</script>

<style lang="scss" scoped>
@import 'ag-grid-community/styles//ag-grid.css';
@import 'ag-grid-community/styles//ag-theme-alpine.css';

.ag-theme-alpine {
  --ag-font-family: $default-font03;
}
</style>